<template>
    
        <div id="content">
            
            <div class="search_body">
                <div class="search_input">
                    <div class="search_input_wrapper">
                        <i class="iconfont icon-sousuo"></i>
                        <input type="text" v-model="message">
                    </div>					
                </div>
                <div class="search_result">
                    <h3>电影/电视剧/综艺</h3>
                    <ul>
                        <li v-for="cinemas in cinemasList" :key="cinemas.cinemaId">
                            <!-- <div class="img"><img src="images/movie_1.jpg"></div> -->
                            <div class="info">
                                <p><span>{{cinemas.name}}</span><span>{{cinemas.lowPrice/100}}元</span></p>
                                <p>{{cinemas.address}}</p>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
</template>

<script>
export default {
    name:'Search',
    data() {
        return {
            message:'',
            /* cinemasList:{
            isFirst:true,
            isLoading:false,
            errMsg:'',
            users:[]
            } */
            cinemasList:[]
        }
    },
    methods: {
        cancelRequest(){
            if (typeof this.source === 'function') {
                // console.log( typeof this.source);
                // console.log('function');
                // console.log(typeof this.source === 'function');
                this.source('终止请求')
            }
        }
    },
    watch:{
        //连续按键取消多次请求
        message(newVal){
            var that = this
            this.cancelRequest()
            console.log(newVal);
            this.axios({
            type:"GET",
          url:"https://m.maizuo.com/gateway?cityId=410100&ticketFlag=0&k=2621793"+newVal,
          cancelToken:new this.axios.CancelToken(function(c){that.source = c;}),
          headers:{
              'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.0.4","e":"16348321693295459686744065"}',
				'X-Host': 'mall.film-ticket.cinema.list'
          }
      }).then((res)=>{
        // console.log(res.data.data);
        // console.log(res.data.data.cinemas);
        var msg = res.data.data;
        var cinemas = res.data.data.cinemas
        if (msg && cinemas) {
            this.cinemasList = res.data.data.cinemas
            // console.log(this.cinemasList);
        }
      }).catch((err)=>{
          if (this.axios.isCancel(err)) {
              //请求取消返回
              console.log('Rquest canceled',err.message);
            //   console.log(this.axios.isCancel(err));
          }else{
              console.log(err);
          }
      })
        }
    }
}
</script>

<style scoped>
#content .search_body{ flex:1; overflow:auto;}
.search_body .search_input{ padding: 8px 10px; background-color: #f5f5f5; border-bottom: 1px solid #e5e5e5;}
.search_body .search_input_wrapper{ padding: 0 10px; border: 1px solid #e6e6e6; border-radius: 5px; background-color: #fff; display: flex; line-height: 20px;}
.search_body .search_input_wrapper i{font-size: 16px; padding: 4px 0;}
.search_body .search_input_wrapper input{ border: none; font-size: 13px; color: #333; padding: 4px 0; outline: none; margin-left: 5px; width:100%;}
.search_body .search_result h3{ font-size: 15px; color: #999; padding: 9px 15px; border-bottom: 1px solid #e6e6e6;}
.search_body .search_result li{ border-bottom:1px #c9c9c9 dashed; padding: 10px 15px; box-sizing:border-box; display: flex;}
.search_body .search_result .img{ width: 60px; float:left; }
.search_body .search_result .img img{ width: 100%; }
.search_body .search_result .info{ float:left; margin-left: 15px; flex:1;}
.search_body .search_result .info p{ height: 22px; display: flex; line-height: 22px; font-size: 12px;}
.search_body .search_result .info div{ height: 22px; display: flex; line-height: 22px; font-size: 12px;}
.search_body .search_result .info p:nth-of-type(1) span:nth-of-type(1){ font-size: 18px; flex:1; }
.search_body .search_result .info p:nth-of-type(1) span:nth-of-type(2){ font-size: 16px; color:#fc7103;}
</style>